body {
  margin: 0;
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
  font-family: sans-serif;
  background: url(components/images/background-image-1.jpg);
  background-repeat: no-repeat;
  object-fit: cover;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.today {
  scale: 1.5;
}
.head {
  line-height: 40px;
  border-radius: 25px;
  backdrop-filter: blur(10px);
  background: linear-gradient(90deg, #00000030,#ffffff15);
}
.App {
  text-align: center;
  transition: all .5s;
}
.main h2 ,.text {
  text-shadow: 0px 0px 20px #ffffff3c;
}
.main {
  box-sizing: border-box;
  margin: auto;
  color: #ffffff;
  margin-bottom: 90px;
  padding-bottom: 30px;
  padding: 10px;
  width: 85vw;
  max-width: 600px;
  height: fit-content;
  display: grid;
  place-items: center;
  background: linear-gradient(90deg, #00000030,#ffffff15);
  border-radius: 10px;
  animation: glow 20s linear infinite;
}
@keyframes glow {
  0% {
    backdrop-filter: blur(40px);
  }
  20% {
    backdrop-filter: blur(0px) contrast(2);
  }
  40% {
    backdrop-filter: blur(0px);
  }
  60% {
    backdrop-filter: blur(0px) hue-rotate(60deg) contrast(2);
  }
  80% {
    backdrop-filter: blur(0px) hue-rotate(60deg);
  }
  100% {
    backdrop-filter: blur(40px);
  }
}
.main div {
  width: 70vw;
  max-width: 300px;
  margin: 5px;
  padding: 10px;
  font-size: 22px;
  transition: all .5s;
  color: #e0e0e0;
  backdrop-filter: blur(40px);
  background: linear-gradient(90deg, #00000030,#ffffff15);
  border-radius: 15px;
  text-shadow: 0px 0px 20px #00000067;
  animation: load .5s alternate ease-in-out;
  position: relative;
  transition: all .5s;
}
.main div:hover {
  box-shadow: none;
  background: #00000075;
}

@keyframes load {
  0% {
    filter: blur(100px);
  }
  100% {
    filter: blur(0px);
  }
}

a {
  color: cyan;
}

.days {
  box-sizing: border-box;
  padding: 10px;
  margin: auto;
  margin-bottom: 10px;
  width: fit-content;
  min-width: 300px;
  display: flex;
  gap: 3px;
  justify-content: space-around;
  border-radius: 5px;
}
.days button {
  color: #ffffff;
  box-sizing: border-box;
  border-radius: 10px;
  border: 2px solid transparent;
  padding: 10px 5px;
  background: #00000030;
  backdrop-filter: blur(10px);
  transition: all .5s;
}
.days button:hover {
  border: 2px solid white;
}

::-webkit-scrollbar {
  display: none;
}
@media (max-width: 700px) {
  .days {
    width: 85vw;
    overflow: hidden;
    overflow-x: scroll;
  }
}


.outer {
  width: 150px;
  height: 150px;
  position: relative;
  display: grid;
  place-items: center;
  font-size: 20px;
}
.inner {
  border-radius: 50%;
  border-bottom: 5px dotted #000000;
  width: 150px;
  height: 150px;
  position: absolute;
  top: 0px;
  animation: loading 1s alternate ease-in-out;
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.chngesBlock {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  padding: 10vw;
  position: fixed;
  backdrop-filter: blur(20px);
  background: linear-gradient(45deg, #00000030, #00000035);
  top: 0px;
  left: 0px;
  animation: blur .5s alternate ease-in-out;
}
@keyframes blur {
  0% {
    backdrop-filter: blur(0px);
  }
  100% {
    backdrop-filter: blur(20px);
  }
}

.chngesBlock div {
  margin: 20px 0px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  place-items: center;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  border-radius: 10px;
  background: #00000030;
}
.chngesBlock div button {
  height: 35px;
  min-width: 35px;
  border: none;
  padding: 0px 10px;
  border-radius: 10px;
}
.xmark {
  scale: 2;
  cursor: pointer;
  border-radius: 50%;
  transition: all .5s;
  background: white;
}
.xmark:hover {
  scale: 2.5;
  transform: rotate(90deg);
}